.IconBox {
	color           : #f2f2f2;
	display         : -ms-flexbox;
	display         : flex;
	-ms-flex-pack : center;
	    justify-content : center;
	-ms-flex-align     : center;
	    align-items     : center;
	height          : 100%;
	-ms-flex-flow       : column;
	    flex-flow       : column;
	padding         : 1em;
}

.IconBox:hover {
	text-decoration : none;
}

.IconBox.hvr-sweep-to-right, .IconBox.hvr-shutter-out-horizontal {
	display : -ms-flexbox;
	display : flex;
}

.IconBox.hvr-sweep-to-right:before, .IconBox.hvr-shutter-out-horizontal:before {
	background-color : rgba(255, 255, 255, 0.1);
}


