.Contact {
	color       : #ffffff;
	font-family : "Helvetica Neue", Helvetica, sans-serif;
	display     : -ms-flexbox;
	display     : flex;
	-ms-flex-flow   : column;
	    flex-flow   : column;
}

.Contact-Header {
	background-color : var(--blue);
	padding          : 1em;
	text-align       : center;
	-ms-flex             : 0 1;
	    flex             : 0 1;
}

.Contact-Header p {
	margin     : 0;
	color      : #eeeeee;
	font-style : italic;
	padding    : 1em;
}

.Contact-Body {
	background-color : #5cb3fd;
	padding          : 1em;
	-ms-flex             : 1 1;
	    flex             : 1 1;
	display          : -ms-flexbox;
	display          : flex;
	-ms-flex-flow        : column;
	    flex-flow        : column;
	-ms-flex-pack  : distribute;
	    justify-content  : space-around;
	text-align       : center;
}

.Contact-Body > section {
	margin : 1em 0;
}

.Contact-Body p {
	margin : 0;
}

.Contact-Body ul li {
	margin : .5em 0;
}

.Contact-Body ul li i {
	margin-right : .5em;
}


