:root {
	--bg-gray    : #404040;
	--gray       : #a6a6a6;
	--light-gray : #4e4e4e;
	--orange     : #e99756;
	--blue       : #0275d8;
	--red        : #e96656;
	--purple     : #9756e9;
	--green      : #5cb85c;
}

.bg-green {
	background-color : var(--green);
}

.bg-light-gray { background-color : var(--light-gray) }

.bg-gray { background-color : var(--bg-gray) }

.bg-orange {
	background-color : var(--orange);
}

.bg-red { background-color : var(--red); }

.bg-purple { background-color : var(--purple) }

.bg-blue { background-color : var(--blue) }

.GridContainer {
	display               : grid;
	grid-template-rows    : auto;
	justify-items         : stretch;
	grid-template-columns : 1fr 1fr;
	grid-template-areas   : "logo logo" "title title" "download download" "position position" "linkedin linkedin" "nav nav" "route-container route-container" "footer footer"
}

@media screen and (min-width : 576px) {
	.GridContainer {
		grid-template-columns : 1fr 1fr 1fr 1fr;
		grid-template-areas   : "logo logo logo logo" "title title title download" "position position position linkedin" "nav nav nav nav" "route-container route-container route-container route-container" "footer footer footer footer"
	}
}

@media screen and (min-width : 768px) {
	.GridContainer {
		grid-template-columns : 2fr 1fr 1fr 1fr 1fr;
		grid-template-areas   : "logo title title title download" "logo position position position linkedin" "logo nav nav nav nav" "route-container route-container route-container route-container route-container" "footer footer footer footer footer";
	}

	.logo-area {
		display     : -ms-flexbox;
		display     : flex;
		-ms-flex-align : center;
		    align-items : center;
	}
}

.RouteContainer {
	grid-area : route-container;
}

.footer-area {
	padding          : 1em;
	background-color : var(--bg-gray);
	color            : #ffffff;
	grid-area        : footer;
}

.Logo {
	max-height : 350px;
}

.logo-area {
	grid-area     : logo;
	text-align    : center;
	border-bottom : 1px solid var(--gray);
}

.title-area {
	grid-area : title;
	font-size : 40px;
}

.download-area {
	grid-area : download;
}

.position-area {
	grid-area : position;
	font-size : 36px;
}

.nav-area {
	grid-area : nav;
}

.linkedin-area {
	grid-area : linkedin;
}

.HeaderBox {
	color          : var(--gray);
	font-family    : "Dosis", "Helvetica Neue", Helvetica, sans-serif;
	text-transform : uppercase;
	padding        : 1em;
	display        : -ms-flexbox;
	display        : flex;
	-ms-flex-align    : center;
	    align-items    : center;
}

.SocialMedia {
	display         : -ms-flexbox;
	display         : flex;
	-ms-flex-pack : justify;
	    justify-content : space-between;
}



