.Skills {
	display               : grid;
	grid-template-columns : 1fr;
	grid-template-rows    : auto;
	grid-template-areas   : "header" "skill1" "skill2" "skill3";
	color                 : #ffffff;
	padding               : 1em;
}

@media screen and (min-width : 768px) {
	.Skills {
		grid-template-columns : 1fr 1fr 1fr;
		grid-template-rows    : auto;
		grid-row-gap          : 15px;
		grid-template-areas   : "header header header" "skill1 skill2 skill3";
	}
}

.Skill {
	display     : -ms-flexbox;
	display     : flex;
	-ms-flex-flow   : column;
	    flex-flow   : column;
	-ms-flex-align : center;
	    align-items : center;
}

.Experience-header {
	grid-area : header;
}

.skill-1 {
	grid-area : skill1
}

.skill-2 {
	grid-area : skill2;
}

.skill-3 {
	grid-area : skill3;
}


